import React, { Component } from 'react';
import AppSection from './components/AppSection';
import ProfileSection from './components/ProfileSection';
import Navigation from './components/Navigation';
import image1 from './images/image_001.jpg';
import image2 from './images/image_002.jpg';
import image3 from './images/image_003.jpg';
import image4 from './images/image_004.jpg';
import image5 from './images/image_005.jpg';


class App extends Component {

  constructor() {
    super();
    this.state = {
      appsList: [],
      images: [image1, image2, image3, image4, image5],
    };
  }

  componentDidMount() {
    this.setState({ appsList: require('./data/data.json') });
  }

  render() {

    return (
      <div className="bg-gray-100 h-screen">
        <Navigation />    
        {          
          this.state.appsList.map(app => {
            return (
              <AppSection 
                app={app}
                key={app.sectionId}
                images={this.state.images}
             />
            )
          })
        }   
        <ProfileSection/>
      </div>
    );
  }
  
}

export default App;
