import React from 'react';
import '../css/section.css';
import video1 from '../videos/video_001.mp4';
import Carousel from 'react-bootstrap/Carousel';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons';
import { FaGooglePlay, FaAmazon } from 'react-icons/fa';


function AppSection(props) {

  var appTitle = props.app.appTitle;
  var appDescription = props.app.appDescription;
  var images = props.images;
  var sectionId = props.app.sectionId;
  
  var classes = sectionId + " vh-min-100 d-flex justify-content-around section-container";

  var previousIcon = <span className="btn btn-dark button-left">
    <FontAwesomeIcon icon={faChevronLeft}/>
  </span>

  var NextIcon = <span className="btn btn-dark button-right">
      <FontAwesomeIcon icon={faChevronRight} />
    </span>

  return (
    <div className={classes} id={sectionId}>

      <div className="description col-md-4">
        <div className="card">
          <div className="card-header section1 d-flex justify-content-between">
              {appTitle} 
              <div>
                <FaGooglePlay className="mr-4"/>
                <FaAmazon className="mr-2"/>
              </div>
          </div>
          
          <div className="card-body section1">
              <p>{appDescription}</p>
          </div>
        </div>
      </div>

        <div className="video">
          <div className="left-btns">
              <div className="left-btn1"></div>
              <div className="left-btn2"></div>
              <div className="left-btn3"></div>
          </div>
          <div className="bg-1">
              <div className="notch">
                  <div className="speaker"></div>
                  <div className="sensor"></div>
              </div>
              <video controls preload="metadata">
                  <source src={video1} type="video/mp4"/>
              </video>
          </div>
          <div className="right-btns">
              <div className="right-btn1"></div>
          </div>
        </div>


        <div id="images-main" className="images-main carousel slide" data-ride="carousel">
            <div className="left-btns">
                <div className="left-btn1"></div>
                <div className="left-btn2"></div>
                <div className="left-btn3"></div>
            </div>
            <div className="bg-1">
                <div className="notch">
                    <div className="speaker"></div>
                    <div className="sensor"></div>
                </div>

                <div className="images carousel-inner">
                  <Carousel 
                    prevIcon={previousIcon}
                    nextIcon={NextIcon}>
                      {
                        images.map(image =>{
                          return (
                            <Carousel.Item >
                              <img
                                className="d-block w-100"
                                src={image}
                                alt="First slide"
                              />
                            </Carousel.Item>
                          )
                        })
                      }                                          
                  </Carousel>
                </div>
            </div>
            <div className="right-btns">
                <div className="right-btn1"></div>
            </div>
        </div>
      </div>
  );
}

export default AppSection;
